import request from '@/utils/request'

//id
export function joinAjax(data) {
    return request({
      url: '/rest/join',
      method: 'post',
      data:data
    })
  }
  
  //
export function useCoupon(data) {
  return request({
    url: '/rest/useCoupon',
    method: 'post',
    data:data
  })
}

//
export function uploadPic(data) {
  return request({
    url: '/rest/uploadPic',
    method: 'post',
    data:data
  })
}

 



